            }
        },
      mounted() {
        $(".PcbHome").on("click",function (event) {event.stopPropagation();$(this).find(".selectNumber ul").hide();});
        $(".PcbHome .selectNumber .input").on("click",function(event) {$(".PcbHome").find(".selectNumber ul").hide();$(this).parents(".selectNumber").find(".list ul").show();event.stopPropagation();});
        $(".PcbHome .selectNumber li").on("click",function(event){event.stopPropagation();$(this).parents(".selectNumber").find(".input p").text($(this).text());$(this).parents("ul").hide();})
        $(".option p").click(function(){$(this).addClass("select");$(this).siblings().removeClass("select")})
        $(".opboxtion p").click(function(){if($(this).hasClass("select")){$(this).removeClass("select")}else{$(this).addClass("select");}})


      },
        methods: {
          //下一步跳转
          nextStep(){
            console.log("1");
            this.$router.push({
              path:'PcbParameter'
            })
